<template>
  <form class="form">
    <h1 class="form__title">
      Создать профиль
    </h1>

    <h3 class="form__title--1">
      Личные данные
    </h3>

    <div class="form--flex">
      <div class="form--mr">
        <p class="form__name">
          Фамилия*
        </p>
        <v-inpt
          :value="lastname"
          :error="errors.lastname"
          @changeInput="lastname = $event, clearError('lastname')"
          holder="Иванов"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Имя*
        </p>
        <v-inpt
          :value="firstname"
          :error="errors.firstname"
          @changeInput="firstname = $event, clearError('firstname')"
          holder="Иван"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Отчество
        </p>
        <v-inpt
          :value="patronymic"
          :error="errors.patronymic"
          @changeInput="patronymic = $event, clearError('patronymic')"
          holder="Иванович"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Дата рождения*
        </p>
        <v-inpt
          :value="birthday"
          :error="errors.birthday"
          @changeInput="birthday = $event, clearError('birthday')"
          type="date"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Номер телефона*
        </p>
        <v-inpt
          :value="phone"
          :error="errors.phone"
          :max="11"
          :min="11"
          @changeInput="phone = $event, clearError('phone')"
          holder="7**********"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Пол
        </p>
        <v-selct
          :array="['Мужской', 'Женский']"
          :value="selectGender"
          @changeSelect="selectGender = $event"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Группа клиентов*
        </p>
        <v-selct
          :array="['VIP', 'Проблемные', 'ОМС']"
          :value="selectGroup"
          @changeSelect="selectGroup = $event"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Лечащий врач
        </p>
        <v-selct
          :array="['Не выбрано', 'Иванов', 'Захаров', 'Чернышева']"
          :value="selectDoctor"
          @changeSelect="selectDoctor = $event"
        />
      </div>
    </div>

    <v-ckbx
      id="sms"
      :value="checkboxValue"
      class="form__check"
    >
      Не отправлять СМС.
    </v-ckbx>

    <h3 class="form__title--1">
      Адрес
    </h3>

    <div class="form--flex">
      <div class="form--mr">
        <p class="form__name">
          Индекс
        </p>
        <v-inpt
          :value="index"
          :error="errors.index"
          :max="6"
          :min="6"
          @changeInput="index = $event, clearError('index')"
          holder="123456"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Страна
        </p>
        <v-inpt
          :value="country"
          :error="errors.country"
          @changeInput="country = $event, clearError('country')"
          holder="Россия"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Область
        </p>
        <v-inpt
          :value="region"
          :error="errors.region"
          @changeInput="region = $event, clearError('region')"
          holder="Московская"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Город*
        </p>
        <v-inpt
          :value="city"
          :error="errors.city"
          @changeInput="city = $event, clearError('city')"
          holder="Москва"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Улица
        </p>
        <v-inpt
          :value="street"
          :error="errors.street"
          @changeInput="street = $event, clearError('street')"
          holder="Воздвиженка"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Дом
        </p>
        <v-inpt
          :value="home"
          :error="errors.home"
          @changeInput="home = $event, clearError('home')"
          holder="3/5"
          class="form__input"
        />
      </div>
    </div>

    <h3 class="form__title--1">
      Документ
    </h3>

    <div class="form--flex">
      <div class="form--mr">
        <p class="form__name">
          Тип документа*
        </p>
        <v-selct
          :array="['Паспорт', 'Свидетельство о рождении', 'Вод. удостоверение']"
          :value="selectDocument"
          @changeSelect="selectDocument = $event"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Серия
        </p>
        <v-inpt
          :value="series"
          :error="errors.series"
          :min="4"
          :max="4"
          @changeInput="series = $event, clearError('series')"
          holder="1122"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Номер
        </p>
        <v-inpt
          :value="numberPass"
          :error="errors.numberPass"
          :min="6"
          :max="6"
          @changeInput="numberPass = $event, clearError('numberPass')"
          holder="788888"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Кем выдан
        </p>
        <v-inpt
          :value="issuedBy"
          :error="errors.issuedBy"
          @changeInput="issuedBy = $event, clearError('issuedBy')"
          holder="МВД по г.Москве"
          class="form__input"
        />
      </div>

      <div class="form--mr">
        <p class="form__name">
          Дата выдачи*
        </p>
        <v-inpt
          :value="dateIssue"
          :error="errors.dateIssue"
          @changeInput="dateIssue = $event, clearError('dateIssue')"
          type="date"
          holder="01.01.01"
          class="form__input"
        />
      </div>
    </div>

    <p class="form--attention">
      * - Поле обязательное для заполнения
    </p>

    <v-btn
      @click="validate()"
      class-el="btn--default form__btn"
    >
      Создать
    </v-btn>

  </form>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'FormComponent',
  data () {
    return {
      lastname: '',
      firstname: '',
      patronymic: '',
      birthday: '',
      phone: '',
      index: '',
      country: '',
      region: '',
      city: '',
      street: '',
      home: '',
      series: '',
      numberPass: '',
      issuedBy: '',
      dateIssue: '',
      nameRegExp: /\d/i,
      phoneRegExp: /^7{1}\d{10}$/i,
      indexRegExp: /\d{6}/i,
      homeRegExp: /\/{2,}/i,
      spaceRegExp: /\s{2,}/i,
      symbolsRegExp: /[.,:'"!@#$%^&*()=+\\|/?<>]/i,
      selectGender: 'Мужской',
      selectDoctor: 'Не выбрано',
      selectGroup: 'VIP',
      selectDocument: 'Паспорт',
      checkboxValue: false,
      errors: {
        lastname: {
          status: false,
          text: ''
        },
        firstname: {
          status: false,
          text: ''
        },
        patronymic: {
          status: false,
          text: ''
        },
        birthday: {
          status: false,
          text: ''
        },
        phone: {
          status: false,
          text: ''
        },
        index: {
          status: false,
          text: ''
        },
        country: {
          status: false,
          text: ''
        },
        region: {
          status: false,
          text: ''
        },
        city: {
          status: false,
          text: ''
        },
        street: {
          status: false,
          text: ''
        },
        home: {
          status: false,
          text: ''
        },
        series: {
          status: false,
          text: ''
        },
        numberPass: {
          status: false,
          text: ''
        },
        issuedBy: {
          status: false,
          text: ''
        },
        dateIssue: {
          status: false,
          text: ''
        }
      }
    }
  },
  methods: {
    ...mapMutations(['CHANGE_STATE']),

    openPopup () {
      this.CHANGE_STATE({
        type: 'popup',
        data: {
          bg: true,
          box: true
        }
      })
    },

    clearError (type) {
      this.errors[type].status = false
      this.errors[type].text = ''
    },

    validate () {
      let errorsCount = 0

      let date = new Date()
      const year = date.getFullYear()
      /**
       * фаимилия
       */
      if (!this.lastname.length) {
        this.errors.lastname.status = true
        this.errors.lastname.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (this.lastname.length < 4) {
        this.errors.lastname.status = true
        this.errors.lastname.text = 'Минимальная длина фаимилии 4 символов'
        errorsCount++
      }
      else if (this.nameRegExp.test(this.lastname)) {
        this.errors.lastname.status = true
        this.errors.lastname.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.lastname) || this.symbolsRegExp.test(this.lastname)) {
        this.errors.lastname.status = true
        this.errors.lastname.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * имя
       */
      if (!this.firstname.length) {
        this.errors.firstname.status = true
        this.errors.firstname.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (this.firstname.length < 4) {
        this.errors.firstname.status = true
        this.errors.firstname.text = 'Минимальная длина имени 4 символа'
        errorsCount++
      }
      else if (this.nameRegExp.test(this.firstname)) {
        this.errors.firstname.status = true
        this.errors.firstname.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.firstname) || this.symbolsRegExp.test(this.firstname)) {
        this.errors.firstname.status = true
        this.errors.firstname.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * отчество
       */
      if (this.nameRegExp.test(this.patronymic)) {
        this.errors.patronymic.status = true
        this.errors.patronymic.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.patronymic) || this.symbolsRegExp.test(this.patronymic)) {
        this.errors.patronymic.status = true
        this.errors.patronymic.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * день рождения
       */
      const birthday = this.birthday.split('-')
      if (!this.birthday.length) {
        this.errors.birthday.status = true
        this.errors.birthday.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (+birthday[0] > year || +birthday[0] < 1920) {
        this.errors.birthday.status = true
        this.errors.birthday.text = 'Некорректный год'
        errorsCount++
      }
      /**
       * телефон
       */
      if (!this.phone.length) {
        this.errors.phone.status = true
        this.errors.phone.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (!this.phoneRegExp.test(this.phone)) {
        this.errors.phone.status = true
        this.errors.phone.text = 'Номер телефона должен начинаться с 7 и состоять из 11 чисел'
        errorsCount++
      }
      /**
       * индекс
       */
      if (!this.indexRegExp.test(this.index) && this.index.length) {
        this.errors.index.status = true
        this.errors.index.text = 'В индексе должно быть 6 цифр'
        errorsCount++
      }
      /**
       * страна
       */
      if (this.nameRegExp.test(this.country) && this.country.length) {
        this.errors.country.status = true
        this.errors.country.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.country.length < 4 && this.country.length) {
        this.errors.country.status = true
        this.errors.country.text = 'Минимальная длина названия страны 4 символа'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.country) || this.symbolsRegExp.test(this.country)) {
        this.errors.country.status = true
        this.errors.country.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * область
       */
      if (this.nameRegExp.test(this.region) && this.region.length) {
        this.errors.region.status = true
        this.errors.region.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.region.length < 4 && this.region.length) {
        this.errors.region.status = true
        this.errors.region.text = 'Минимальная длина названия облости 4 символа'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.region) || this.symbolsRegExp.test(this.region)) {
        this.errors.region.status = true
        this.errors.region.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * город
       */
      if (!this.city.length) {
        this.errors.city.status = true
        this.errors.city.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (this.city.length < 3) {
        this.errors.city.status = true
        this.errors.city.text = 'Минимальная длина названия города 3 символа'
        errorsCount++
      }
      else if (this.nameRegExp.test(this.city)) {
        this.errors.city.status = true
        this.errors.city.text = 'Поле может содержать только буквы'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.city) || this.symbolsRegExp.test(this.city)) {
        this.errors.city.status = true
        this.errors.city.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * улица
       */
      if (this.street.length < 4 && this.street.length) {
        this.errors.street.status = true
        this.errors.street.text = 'Минимальная длина названия улицы 4 символа'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.street) || this.symbolsRegExp.test(this.street)) {
        this.errors.street.status = true
        this.errors.street.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * дом
       */
      if (this.homeRegExp.test(this.home) && this.home.length) {
        this.errors.home.status = true
        this.errors.home.text = 'Номер дома может содержать только цифры и один знак /'
        errorsCount++
      }
      else if (this.home.length > 5 && this.home.length) {
        this.errors.home.status = true
        this.errors.home.text = 'Максимальная длина номера дома 5 символов'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.home) || this.symbolsRegExp.test(this.home)) {
        this.errors.home.status = true
        this.errors.home.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * серия
       */
      if (!this.nameRegExp.test(this.series) && this.series.length) {
        this.errors.series.status = true
        this.errors.series.text = 'Серия паспорта может содержать только цифры'
        errorsCount++
      }
      else if ( this.series.length !== 4 && this.series.length) {
        this.errors.series.status = true
        this.errors.series.text = 'Серия паспорта содержит ровно 4 цифры'
        errorsCount++
      }
      /**
       * номер паспорта
       */
      if (!this.nameRegExp.test(this.numberPass) && this.numberPass.length) {
        this.errors.numberPass.status = true
        this.errors.numberPass.text = 'Номер паспорта может содержать только цифры'
        errorsCount++
      }
      else if ( this.numberPass.length !== 6 && this.numberPass.length) {
        this.errors.numberPass.status = true
        this.errors.numberPass.text = 'Номер паспорта содержит ровно 6 цифр'
        errorsCount++
      }
      /**
       * кем выдан
       */
      if ( this.issuedBy.length < 6 && this.issuedBy.length) {
        this.errors.issuedBy.status = true
        this.errors.issuedBy.text = 'Минимальная длина поля 6 символов'
        errorsCount++
      }
      else if (this.spaceRegExp.test(this.issuedBy) || this.symbolsRegExp.test(this.issuedBy)) {
        this.errors.issuedBy.status = true
        this.errors.issuedBy.text = 'Поле не может содержать несколько пробелов подряд или спецсимволы'
        errorsCount++
      }
      /**
       * дата выдачи
       */
      const dateIssueYear = this.dateIssue.split('-')
      if (!this.dateIssue.length) {
        this.errors.dateIssue.status = true
        this.errors.dateIssue.text = 'Поле обязательно для заполнения'
        errorsCount++
      }
      else if (+dateIssueYear[0] > year || +dateIssueYear[0] < 1920) {
        this.errors.dateIssue.status = true
        this.errors.dateIssue.text = 'Некорректный год'
        errorsCount++
      }

      if (!errorsCount) {
        this.openPopup()
        this.lastname = ''
        this.firstname = ''
        this.patronymic = ''
        this.birthday = ''
        this.phone = ''
        this.index = ''
        this.country = ''
        this.region = ''
        this.city = ''
        this.street = ''
        this.home = ''
        this.series = ''
        this.numberPass = ''
        this.issuedBy = ''
        this.dateIssue = ''
      }
    }
  }
}
</script>

