<template>
  <div
    :class="{ 'chckbox--error' : error && error.status }"
    class="chckbox"
  >
    <label
      :for="id"
      class="chckbox__label"
    >
      <input
        :id="id"
        v-model="name"
        @change="$emit('changeCheck', $event.target.checked)"
        type="checkbox"
        :class="classEl === '' ?'chckbox__input' : classEl"
      >

      <span class="chckbox__box">
        <i
          v-if="name"
          class="fas fa-check"
        />
      </span>

      <span class="chckbox__text">
        <slot />
      </span>
    </label>

    <transition
      appear
      name="slide-top"
      mode="out-in"
    >
      <span
        v-if="error && error.status"
        class="chckbox__error"
      >
        {{ error.text }}
      </span>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'CheckboxComponent',
    props: {
      classEl: {
        type: String,
        default: ''
      },
      value: {
        type: Boolean,
        required: true
      },
      id: {
        type: String,
        required: true
      },
      error: {
        type: Object,
        default: () => { return {} }
      }
    },
    data () {
      return {
        name: this.value
      }
    },
    watch: {
      value (data) {
        this.name = data
      }
    }
  }
</script>
