import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false
Vue.config.devtools = true

import Input from './components/ui-elements/input-component'
import Select from './components/ui-elements/select-component'
import Button from './components/ui-elements/button-component'
import Checkbox from './components/ui-elements/checkbox-component'

Vue.component('v-inpt', Input)
Vue.component('v-selct', Select)
Vue.component('v-btn', Button)
Vue.component('v-ckbx', Checkbox)

import store from './store'

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
