<template>
  <div
    :class="[classPr]"
    class="inpt"
  >
    <input
      :class="[classEl, 'inpt__input', { 'inpt--error' : error && error.status }]"
      :type="type"
      :placeholder="holder"
      :disabled="disabled"
      v-model.trim="name"
      @input="$emit('changeInput', $event.target.value)"
      :minlength="min"
      :maxlength="max"
    >

    <transition
      appear
      name="slide-top"
      mode="out-in"
    >
      <span
        v-if="error && error.text"
        class="inpt__error"
      >
        {{ error.text }}
      </span>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'InputComponent',
  props: {
    type: {
      type: String,
      default: ''
    },
    classPr: {
      type: String,
      default: ''
    },
    classEl: {
      type: String,
      default: ''
    },
    holder: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    error: {
      type: Object,
      default: () => ({})
    },
    disabled: {
      type: Boolean,
      default: false
    },
    max: {
      type: Number
    },
    min: {
      type: Number
    }
  },
  data () {
    return {
      name: this.value ? this.value : ''
    }
  },
  watch: {
    value (data) {
      this.name = data
    }
  },
  computed: {
    hasPostfix () {
      return !!this.$slots.postfix
    }
  }
}
</script>
