<template>
  <layout-modal
    :bg="popup.bg"
    :box="popup.box"
    @close="close()"
  >
    <div class="popup">
      <v-btn
        @click="close()"
        class-el="popup--close"
      >
        <i class="fas fa-times"></i>
      </v-btn>
      Отлично! Ваши даннные сохранены
    </div>
  </layout-modal>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import LayoutModal from './layout'

export default {
  name: 'PopupComponent',
  components: {
    LayoutModal
  },
  computed: {
    ...mapState(['popup'])
  },
  methods: {
    ...mapMutations(['CHANGE_STATE']),

    close () {
      this.CHANGE_STATE({
        type: 'popup',
        data: {
          bg: false,
          box: false
        }
      })
    }
  }
}
</script>
