<template>
  <div
    @click="toggleDrop"
    :class="[{ 'slct--error' : error && error.status }, { 'active' : isOpen }]"
    class="slct"
    @mouseleave="close"
  >
    <div
      :class="[classEl, 'slct__select']"
    >
      <span class="slct__holder">
        {{ holder && !value ? holder : value }}
      </span>

      <div class="slct__wrap">
        <div
          ref="slctBox"
          class="slct__box"
        >
          <div class="slct__list">
            <div
              v-for="(item, i) in array"
              :key="i"
              @click.stop="changeSelect(item)"
              class="slct__item"
            >
              {{ item }}
            </div>
          </div>
        </div>
      </div>

      <div class="slct__arrow">
        <i class="fas fa-caret-down"></i>
      </div>
    </div>

    <span
      v-if="error && error.status"
      class="inpt__error"
    >
      {{ error.text }}
    </span>
  </div>
</template>

<script>
export default {
  name: 'SelectComponent',
  props: {
    id: {
      type: String,
      default: ''
    },
    error: {
      type: Object,
      default: () => ({})
    },
    value: {
      type: String,
      required: true
    },
    holder: {
      type: String,
      default: ''
    },
    array: {
      type: [Object, Array],
      default: () => ({})
    },
    classEl: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isOpen: false
    }
  },
  methods: {
    changeSelect (item) {
      this.$emit('changeSelect', item)
      this.close()
    },

    close () {
      this.isOpen = false
      const child = this.$refs.slctBox
      child.style.height = ``
      return false
    },

    findParent(el, cls) {
      while ((el = el.parentElement) && !el.classList.contains(cls));
      return el;
    },

    toggleDrop (e) {
      this.isOpen = !this.isOpen
      const el = e.target.classList.contains('slct') ? e.target : this.findParent(e.target, 'slct')
      const box = el.children[0].children

      let child = ''
      Array.from(box).forEach((item) => {
        if (item.classList.contains('slct__wrap')) {
          child = item.children[0]
        }
      })
      if (this.isOpen) {
        const scrollHeight = child.scrollHeight
        child.style.height = `${scrollHeight}px`
      } else {
        child.style.height = ``
      }
    }
  }
}
</script>

